@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600&display=swap');

#body {
  background-color: #25282c;
  color: #fff;
  font-family: 'Quicksand', sans-serif;
}


.container {
  max-width: 1295px;
  margin: 0 auto;
}
#customers {
  font-family: 'Quicksand', sans-serif;
  font-size: 16px;
  border-collapse: collapse;
  width: 100%;
  border-radius: 3px;
  overflow: hidden;
}

#customers td, #customers th {
  border: 1px solid #ddd;
  padding: 11px;
}

#customers tr:nth-child(even){background-color: #313539;}

#customers tr:hover {background-color: #25282cb7;}

#customers th {
  padding: 15px 9px;
  text-align: left;
  color: white;
}


.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  background-color: #25282c;
}

.header__itm {
  padding: 3px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}




.students {
  margin: 95px 0;
}



.btns {
  display: flex;
  justify-content: flex-end;
}

.btns button {
  width: 65px;
  padding: 5px;
  margin: 3px;
}

.new-btn {
  background-color: #4285F4;
  color: white;
  border: none;
  padding: 9px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.heading-text {
  font-size: 23px;
  font-weight: 600;

}



.del {
  background-color: #FF3547;
  color: white;
  border: none;
  cursor: pointer;
  font-weight: 500;
  border-radius: 5px;
}


.edit {
  background-color: #FFBB33;
  color: white;
  border: none;
  cursor: pointer;
  font-weight: 500;
  border-radius: 5px;
}



.loading {
  color: white;
  font-size: 45px;
}






@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

.loadModal {
  width:100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 199;
  background-color: #25282cc1;
}

.load {
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 110px auto 0;
  border: solid 10px #4285F4;
  border-radius: 50%;
  border-right-color: transparent;
  border-bottom-color: transparent;
  -webkit-transition: all 0.5s ease-in;
  -webkit-animation-name: rotate;
  -webkit-animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;

  transition: all 0.5s ease-in;
  animation-name: rotate;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}


