.delModal {
   width: 375px;
   height: 205px;
   background-color: white;
   border-radius: 9px;
}

.delModal> p {
   text-align: center;
   color: black;
   font-weight: 600;
   font-size: 21px;
   font-family: 'Quicksand',sans-serif;
}

.modal-btns {
   padding: 15px 75px;
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.modal-btns button {
   width: 75px;
   padding: 3px 15px;
}


.cancelBtn {
   padding: 7px;
   background-color: #4285F4;
   color: white;
   border: none;
   padding: 9px 15px;
   border-radius: 5px;
   cursor: pointer;
}