.modalCover {
   width: 100%;
   height: 100vh;
   display: flex;
   align-items: center;
   justify-content: center;
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   z-index: 5;
   background-color: rgba(18, 18, 18, 0.821);
}

.modalBox {
   width: 735px;
   height: 695px;
   padding: 19px;
   background-color: white;
   border-radius: 7px;
   display: flex;
   flex-direction: column;
   align-items: center;
}

.required {
   width: 301px !important;
   padding: 7px 11px;
   margin-bottom: 25px;
   font-size: 17px;
   font-family: 'Quicksand',sans-serif;
   color: black;
}

label {
   width: 301px;
   padding-bottom: 5px;
   text-align: left;
   color: black;
}



.rowInput {
   width: 100%;
   display: flex;
   align-items: center;
   justify-content: center;
}

.rowInput div {
   width: 50%;
   display: flex;
   flex-direction: column;
   align-items: center;
}


.select {
   width: 225px;
   padding: 7px 11px;
   margin-bottom: 25px;
   font-size: 17px;
   font-family: 'Quicksand',sans-serif;
   color: black;
}

.rowInput-select {
   width: 100%;
   display: flex;
   justify-content: space-between;
}
.rowInput-select div {
   width: 31px;
}


.required:focus {
   outline: none;
}


.del-modal {
   width: 115px;
   padding: 9px;
   background-color: #FF3547;
   color: white;
   border: none;
   cursor: pointer;
   font-weight: 500;
   border-radius: 5px;
 }


 .modal-btns-student {
   width: 295px;
   margin: 25px 0;
   display: flex;
   align-items: center;
   justify-content: space-between;
 }
 